import Vue from "vue";
import VueRouter from "vue-router";
import Index from "./views/Index.vue";
import IndexDark from "./views/IndexDark.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Index",
    component: Index,
  },
  {
    path: "/index-dark",
    name: "IndexDark",
    component: IndexDark,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
