<template>
  <div class="kura_tm_section" id="portfolio">
    <div class="kura_tm_portfolio">
      <div class="container">
        <div class="kura_tm_main_title">
          <span>Portfolio</span>
          <h3>Selected Works</h3>
        </div>
        <div class="portfolio_list gallery_zoom wow fadeInUp" data-wow-duration=".7s">
          <swiper :options="swiperOptions" ref="mySwiper">
            <swiper-slide v-for="image in images" :key="image.src" class="swiper-slide">
              <div class="list_inner">
                <div class="image">
                  <img src="/img/portfolio/410-460.jpg" alt="" />
                  <div class="main" :data-img-url="'' + image.src" />
                  <div class="details">
                    <h3>{{ image.title }}</h3>
                    <span>{{ image.sub }}</span>
                  </div>
                </div>
                <a class="kura_tm_full_link popup-vimeo" @click="onClick(image.videoId)"></a>
              </div>
            </swiper-slide>
          </swiper>
          <div class="kura_tm_swiper_progress fill" slot="navigation">
            <div class="my_pagination_in">
              <span class="current"></span>
              <!-- <span class="pagination_progress"
                ><span class="all"><span></span></span
              ></span> -->
              <span class="total"></span>
            </div>
            <div class="my_navigation">
              <ul>
                <li>
                  <a class="my_prev" href="#"><img class="svg" src="/img/svg/right-arrow.svg" alt="" /></a>
                </li>
                <li>
                  <a class="my_next" href="#"><img class="svg" src="/img/svg/right-arrow.svg" alt="" /></a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "vue-awesome-swiper";
export default {
  name: "Portfolio",
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      swiperOptions: {
        slidesPerView: 3,
        spaceBetween: 3,
        loop: true,
        pagination: false,
        navigation: {
          prevEl: ".my_prev",
          nextEl: ".my_next",
        },
        breakpoints: {
          320: {
            slidesPerView: 1,
            spaceBetween: 20,
          },
          640: {
            slidesPerView: 1,
            spaceBetween: 20,
          },
          768: {
            slidesPerView: 2,
            spaceBetween: 30,
          },
          1024: {
            slidesPerView: 3,
            spaceBetween: 40,
          },
        },
        // Some Swiper option/callback...
      },
      images: [
        {
          src: "/img/portfolio/1.png",
          title: "Oilpump bracket",
          sub: "Designed an original oil pump bracket, created a detailed drawing, printed a prototype, and had it expertly crafted from steel.",
        },
        {
          src: "/img/portfolio/5.png",
          title: "Honda MT cockpit",
          sub: "Replicating Perfection: From sketch to a printed rendition, behold my Honda MT cockpit replica.",
        },
        {
          src: "/img/portfolio/2.png",
          title: "Floor lamp",
          sub: "Handcrafted MDF floor lamp, uniquely designed and lasercut by me.",
        },
        {
          src: "/img/portfolio/4.png",
          title: "Formula 1 circuit",
          sub: "Crafted speed: My Formula 1 circuit design brought to life through laser-cut plexiglass.",
        },
        {
          src: "/img/portfolio/3.png",
          title: "Nerfgun target board",
          sub: "Laser-cut precision meets Nerf excitement with my custom target board design.",
        },
      ],
      modal: false,
      videoId: null,
    };
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.$swiper;
    },
  },
  mounted() {
    // console.log("Current Swiper instance object", this.swiper);
    this.swiper.slideTo(3, 1000, false);
  },
  methods: {
    closeModal() {
      this.modal = false;
    },
    onClick(videoId) {
      this.videoId = videoId;
      this.modal = true;
    },
  },
};
</script>
